import React from "react";
import { Carousel } from "react-bootstrap";
import "./Colores.css"; // Archivo de estilos CSS para el componente

const Colores = () => {
  return (
    <div className="colores-container">
      <div className="jumbotron">
        <h1 className="display-4">Aprende los Colores en Lenguaje de Seas</h1>
        <p className="lead">
          Bienvenido a nuestra seccin de aprendizaje de colores en lenguaje de
          seas.
        </p>
        <hr className="my-4" />
        <p>Explora material terico, videos educativos y ms.</p>
      </div>

      {/* Seccin de Material Terico */}
      <div className="container mt-4">
        <h2>Material Terico</h2>
        <p>
          Aqu encontrars una variedad de recursos tericos sobre colores en
          lenguaje de seas para comenzar tu aprendizaje.
        </p>
        <div className="resource">
          <h3>Introduccin a los Colores</h3>
          <p>
            El lenguaje de seas incluye seas especficas para cada color.
            Aprender estas seas te permitir comunicarte de manera ms
            completa.
          </p>
        </div>
        <div className="resource">
          <h3>Uso de los Colores en Seales</h3>
          <p>
            Los colores son una parte importante del lenguaje de seas. Saber
            cmo sealar los colores correctamente te ayudar a describir
            objetos, ropa y ms.
          </p>
        </div>
      </div>

      {/* Carrusel de Imgenes de los Colores */}
      <div className="container mt-4">
        <h2>Imgenes del Lenguaje de Seas - Colores</h2>
        <Carousel indicators={false} nextLabel="" prevLabel="">
          <Carousel.Item>
            <img
              className="d-block w-100"
              src={require("../components/images/colores/red.jpg")}
              alt="Color Rojo en Lenguaje de Seas"
              style={{ maxHeight: "400px", objectFit: "contain" }}
            />
            <Carousel.Caption>
              <h3>Color Rojo</h3>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <img
              className="d-block w-100"
              src={require("../components/images/colores/orange.jpg")}
              alt="Color Naranja en Lenguaje de Seas"
              style={{ maxHeight: "400px", objectFit: "contain" }}
            />
            <Carousel.Caption>
              <h3>Color Naranja</h3>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <img
              className="d-block w-100"
              src={require("../components/images/colores/yellow.jpg")}
              alt="Color Amarillo en Lenguaje de Seas"
              style={{ maxHeight: "400px", objectFit: "contain" }}
            />
            <Carousel.Caption>
              <h3>Color Amarillo</h3>
            </Carousel.Caption>
          </Carousel.Item>
          {/* Aade ms imgenes segn sea necesario */}
        </Carousel>
      </div>

      {/* Enlaces tiles */}
      <div className="container mt-4 mb-4">
        <h2>Enlaces tiles</h2>
        <ul>
          <li>
            <a href="https://www.nidcd.nih.gov/health/american-sign-language">
              Qu es el lenguaje de seas?
            </a>
          </li>
          <li>
            <a href="https://www.signingsavvy.com/">
              Diccionario en Lnea de Lenguaje de Seas
            </a>
          </li>
          <li>
            <a href="https://www.startasl.com/american-sign-language">
              Curso Gratuito de ASL
            </a>
          </li>
        </ul>
      </div>
    </div>
  );
};

export default Colores;
