import React, { useState } from "react";
import { Carousel } from "react-bootstrap";
import "./Abecedario.css"; // Archivo de estilos CSS para el componente

const Learn = () => {
  const letras = [
    "a",
    "b",
    "c",
    "d",
    "e",
    "f",
    "g",
    "h",
    "i",
    "j",
    "k",
    "l",
    "m",
    "n",
    "o",
    "p",
    "q",
    "r",
    "s",
    "t",
    "u",
    "v",
    "w",
    "x",
    "y",
    "z",
  ];

  const [inputValue, setInputValue] = useState("");
  const [letraImagenes, setLetraImagenes] = useState([]);

  const handleInputChange = (e) => {
    setInputValue(e.target.value.toLowerCase());
  };

  const handleShowImages = () => {
    const inputLetras = inputValue
      .split("")
      .map((char) => (char === " " ? " " : letras.includes(char) ? char : null))
      .filter((n) => n !== null);
    setLetraImagenes(inputLetras);
  };

  const chunkArray = (array, chunkSize) => {
    const results = [];
    while (array.length) {
      results.push(array.splice(0, chunkSize));
    }
    return results;
  };

  const chunks = chunkArray([...letras], 3);

  return (
    <div className="learn-container">
      <div className="jumbotron">
        <h1 className="display-4">Aprende Lenguaje de Seas</h1>
        <p className="lead">
          Bienvenido a nuestra seccin de aprendizaje de lenguaje de seas.
        </p>
        <hr className="my-4" />
        <p>Explora material terico, videos educativos y ms.</p>
      </div>

      {/* Seccin de Material Terico */}
      <div className="container mt-4">
        <h2>Material Terico</h2>
        <p>
          Aqu encontrars una variedad de recursos tericos sobre lenguaje de
          seas para comenzar tu aprendizaje.
        </p>
        <div className="resource">
          <h3>Introduccin al Lenguaje de Seas</h3>
          <p>
            El lenguaje de seas es un sistema de comunicacin visual y gestual
            utilizado por la comunidad sorda y con problemas de audicin.
            Aprender lenguaje de seas no solo facilita la comunicacin, sino
            que tambin promueve la inclusin y el entendimiento cultural.
          </p>
        </div>
        <div className="resource">
          <h3>Gramtica y Estructura</h3>
          <p>
            La gramtica del lenguaje de seas incluye la configuracin de las
            manos, movimientos, ubicacin y expresiones faciales. Aprender la
            estructura del lenguaje de seas es fundamental para comunicarse de
            manera efectiva.
          </p>
        </div>
        <div className="resource">
          <h3>Historia y Cultura</h3>
          <p>
            Conocer la historia y la cultura de la comunidad sorda es
            fundamental para comprender mejor el lenguaje de seas y su impacto
            en la sociedad.
          </p>
        </div>
        <div className="resource">
          <h3>Recursos Adicionales</h3>
          <p>
            Explora ms recursos educativos y libros digitales sobre lenguaje de
            seas para ampliar tu conocimiento.
          </p>
        </div>
      </div>

      {/* Carrusel de Imgenes del Abecedario */}
      <div className="container mt-4">
        <h2>Imgenes del Lenguaje de Seas - Abecedario</h2>
        <Carousel indicators={false} nextLabel="" prevLabel="">
          {chunks.map((chunk, index) => (
            <Carousel.Item key={index}>
              <div className="d-flex justify-content-around">
                {chunk.map((letra) => (
                  <div key={letra} className="p-2">
                    <img
                      className="d-block"
                      src={require(`../components/images/abecedario/${letra}.png`)}
                      alt={`Letra ${letra.toUpperCase()} en Lenguaje de Seas`}
                      style={{ maxHeight: "150px", objectFit: "contain" }}
                    />
                    <Carousel.Caption>
                      <h3>Letra {letra.toUpperCase()}</h3>
                    </Carousel.Caption>
                  </div>
                ))}
              </div>
            </Carousel.Item>
          ))}
        </Carousel>
      </div>

      {/* Seccin de Prctica */}
      <div className="container mt-4">
        <div className="row">
          <div className="col-md-6">
            <h2>Prctica - Escribe tu Nombre</h2>
            <p>
              Ingresa tu nombre para ver cmo se forma en lenguaje de seas:
            </p>
            <input
              type="text"
              value={inputValue}
              onChange={handleInputChange}
              placeholder="Ingresa tu nombre"
              className="form-control"
            />
            <button onClick={handleShowImages} className="btn btn-primary mt-2">
              Mostrar Imgenes
            </button>
          </div>
          <div className="col-md-6 d-flex align-items-center">
            <div className="mt-4 d-flex flex-wrap">
              {letraImagenes.map((letra, index) => (
                <div key={index} className="p-2">
                  {letra === " " ? (
                    <div style={{ width: "50px" }}></div>
                  ) : (
                    <img
                      src={require(`../components/images/abecedario/${letra}.png`)}
                      alt={`Letra ${letra.toUpperCase()} en Lenguaje de Seas`}
                      style={{ maxHeight: "200px", objectFit: "contain" }}
                    />
                  )}
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>

      {/* Enlaces tiles */}
      <div className="container mt-4 mb-4">
        <h2>Enlaces tiles</h2>
        <ul>
          <li>
            <a href="https://www.nidcd.nih.gov/health/american-sign-language">
              Qu es el lenguaje de seas?
            </a>
          </li>
          <li>
            <a href="https://www.signingsavvy.com/">
              Diccionario en Lnea de Lenguaje de Seas
            </a>
          </li>
          <li>
            <a href="https://www.startasl.com/american-sign-language">
              Curso Gratuito de ASL
            </a>
          </li>
        </ul>
      </div>
    </div>
  );
};

export default Learn;
